'use client';

import { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';

// --- Componente para los items del menú ---
// Corregido: La className y onClick se aplican directamente al Link.
const MenuItem = ({ href, children, onClick, isDanger }) => (
  <Link 
    href={href || '#'}
    onClick={onClick}
    className={`block w-full text-left px-4 py-2 text-sm ${isDanger ? 'text-red-600' : 'text-gray-700'} hover:bg-gray-100 hover:text-gray-900 rounded-md transition-colors`}
  >
    {children}
  </Link>
);

// --- Componente Principal del Header ---
const Header = ({ userData }) => {
  const { logout } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const menuRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setIsMenuOpen(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const getInitials = (name) => {
    if (!name) return '?';
    const names = name.split(' ');
    if (names.length > 1) {
      return `${names[0][0]}${names[1][0]}`.toUpperCase();
    }
    return name.substring(0, 2).toUpperCase();
  }

  return (
    <header className="bg-white text-gray-800 p-4 flex justify-between items-center shadow-md sticky top-0 z-50 w-full">

      {/* 1. Logo/Título a la Izquierda */}
      {/* Corregido: Se eliminó la etiqueta <a> anidada */}
      <div className="flex-shrink-0">
        <Link 
          href="/"
          className="text-xl font-bold text-gray-900 hover:text-violet-700 transition-colors"
        >
          Veterinaria Magali Martin
        </Link>
      </div>

      {/* 2. Acciones del Usuario a la Derecha */}
      <div className="flex-shrink-0">
        {userData.isLoggedIn ? (
          <div className="relative" ref={menuRef}>
            <button onClick={() => setIsMenuOpen(!isMenuOpen)} className="flex items-center justify-center bg-gray-200 rounded-full h-10 w-10 text-gray-600 font-bold text-sm focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-violet-500 overflow-hidden">
              {userData.picture ? (
                <Image src={userData.picture} alt="Avatar" width={40} height={40} />
              ) : (
                <span>{getInitials(userData.name)}</span>
              )}
            </button>

            {isMenuOpen && (
              <div className="origin-top-right absolute right-0 mt-2 w-64 rounded-xl shadow-lg bg-white ring-1 ring-black ring-opacity-5 focus:outline-none p-2">
                <div className="px-4 py-3 border-b mb-2">
                  <p className="text-sm font-semibold text-gray-900 truncate">{userData.name}</p>
                  <p className="text-xs text-gray-500 truncate">{userData.email}</p>
                </div>
                
                <div className="space-y-1">
                    <MenuItem href="/mis-datos">Mis Datos</MenuItem>
                    <MenuItem href="/mascotas">Mis Mascotas</MenuItem>
                    <MenuItem href="/mis-turnos">Mis Turnos</MenuItem>
                </div>

                {userData.role === 'admin' && (
                  <div className="py-2">
                    <div className="bg-red-50 rounded-md">
                      <MenuItem href="/admin" isDanger>Panel de Admin</MenuItem>
                    </div>
                  </div>
                )}
                
                <div className="border-t mt-2 pt-2">
                  <MenuItem onClick={logout} isDanger>Cerrar Sesión</MenuItem>
                </div>
              </div>
            )}
          </div>
        ) : (
          /* Corregido: Se eliminó la etiqueta <a> anidada */
          <Link 
            href="/login"
            className="bg-violet-600 hover:bg-violet-700 text-white font-semibold px-4 py-2 rounded-full transition-colors"
          >
            Iniciar Sesión
          </Link>
        )}
      </div>
    </header>
  );
};

export default Header;
